@import "./styles/screensizes";

.lang-de, .lang-fr, .lang-es, .lang-pl {
    .tutorial-container .tutorial .title-container h3 {
        font-size: 9px;
        position: relative;
        top: 3px;
        @include xsm-up {
            font-size: 16px;
        }
    }
    .danger-module h2 {
        top: 12%;
    }
    .popup h2 {
        font-size: 12px;
        text-transform: uppercase;
        position: relative;
        top: 5px;
        left: -5px;
    }
    .glossary-content .glossary-results .result {
        text-transform: uppercase;
        font-size: 16px;
    }
}

.lang-pl {
    .danger-module h2 {
        font-size: 18px;
        top: 17%;
    }
    .play-buttons .play-button {
        font-size: 13px;
    }
    .app-button.green {
        @include xxsm-up {
            font-size: 13px;
        }
    }
    .tutorial-container .tutorial .title-container {
        top: -38px;
        width: 70%;
        text-align: center;
        line-height: 97%;
        height: 37px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

.uc {
    text-transform: uppercase;
}

.large {
    font-size: 24px;
    display: block;
    margin: 5px 0;
}

.familiar-tales {
    position: relative;
    z-index: 1;
    width: 100%;
    min-height: 100%;
    box-sizing: border-box;
    color: black;
    user-select: none;
    @include sm-up {
        width: 465px;
    }
}

.footnote {
    text-align: center;
    font-weight: bold;
    font-size: 12px;
}

.question-mark {
    color: white;
    cursor: help;
    font-size: 20px;
}

.popup-container {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 100001;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    overflow-y: scroll;
    user-select: none;
    @include short-up {
        justify-content: center;
    }
}

.popup {
    background-image: url('./images/interface/popup.png');
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    padding: 10px 0;
    position: relative;
    text-align: center;
    background-size: 200% auto;
    background-position: center center;

    @include xsm-up {
        width: 306px;
        height: 383px;
        background-size: 100% 100%;
    }
    &-close {
        position: absolute;
        right: 20px;
        top: 12px;
        cursor: pointer;
        font-size: 24px;
    }
    h2 {
        font-family: Adonis;
        text-transform: uppercase;
        text-align: center;
        font-size: 18px;
        margin: 10px 0 20px;
    }
    h3 {
        font-family: Adonis;
        text-transform: uppercase;
        font-size: 14px;
        margin: 8px 0 0px;
    }
    p {
        margin: 4px 0;
        font-size: 14px;
    }
    &-content {
        width: 100%;
        box-sizing: border-box;
        padding: 0 30px;
        overflow-y: scroll;
        @include short-up {
            overflow-y: auto;
        }
    }
}

.fw-icon {
    height: 18px;
    width: auto;
    position: relative;
    top: 4px;
}