@import '../../../styles/screensizes';

.entry-display {
    font-size: .875em;
    line-height: 1.4em;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    padding: 0 15px;
    width: 100%;

    @include xxsm-up {
      padding: 0 35px;
    }
    .entry-content {

    }
    .content-text {
        width: 100%;
    }
    .content-option {

    }
    .content-image {

    }
}

.setup-header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 15px;
  .back, .forward {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-width: 90px;
    cursor: pointer;
    img {
      width: 30px;
      height: 18px;
      margin: 0 6px;
      &.setup-header-actions-next {
        transform: scaleX(-1);
      }
    }
    text-transform: uppercase;
    font-weight: bold;
  }
}

.content-image {
    max-width: 80%;
}

.audio-player-container {
    align-self: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    .audio-player-error {
        color: red;
        font-weight: bold;
        text-align: center;
        margin: -10px 0 10px;
    }
}

.audio-player {
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  align-self: center;
  margin: 0 0 20px;
  width: 80%;
  height: 72px;
  .ui {
    opacity: .6;
    cursor: default;
    color: gray;
    width: 40px;
    height: 45px;
    background-size: 100% 100%;
    margin: 0 4px;
    @include xxsm-up {
        margin: 0 12px;
    }
    @include xsm-up {
        margin: 0 18px;
    }
    &.active {
      opacity: 1;
      cursor: pointer;
      color: white;
    }
    &.rw {
        background-image: url('../../../images/interface/audio-rw.png');
    }
    &.ff {
        background-image: url('../../../images/interface/audio-ff.png');
    }
    &.play {
        background-image: url('../../../images/interface/audio-play.png');
    }
    &.pause {
        background-image: url('../../../images/interface/audio-pause.png');
    }
  }
}