@import '../styles/screensizes';

.danger-module {
    background-image: url('../images/danger/box.png');
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: top center;
    width: 190px;
    height: 100px;
    position: relative;
    overflow: fragments;
    @include xxsm-up {
        width: 300px;
        height: 157px;
    }
    h2 {
        color: black;
        padding: 0;
        margin: 0;
        font-size: 20px;
        font-weight: normal;
        position: absolute;
        top: 11%;
        left: 30%;
        width: 51%;
        text-align: center;
        @include xxsm-up {
            font-size: 28px;
        }
    }
    &-minus {
        font-size: 0;
        background-color: transparent;
        border: none;
        position: absolute;
        width: 24px;
        height: 10px;
        cursor: pointer;
        background-image: url('../images/danger/minus.png');
        background-size: 100% 100%;
        top: 51%;
        left: 23%;
        @include xxsm-up {
            left: 31%;
            top: 52%;
        }
    }
    &-value-container {
        width: 21%;
        height: 35%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 41%;
        bottom: 27%;

    }
    &-value {
        height: 100%;
        width: auto;
    }
    &-plus {
        font-size: 0;
        background-color: transparent;
        border: none;
        position: absolute;
        width: 24px;
        height: 24px;
        cursor: pointer;
        background-image: url('../images/danger/plus.png');
        background-size: 100% 100%;
        top: 43.5%;
        right: 19%;
        @include xxsm-up {
            right: 27%;
            top: 47%;
        }
    }
    &-reset {
        font-family: Adonis;
        background-color: transparent;
        border: none;
        color: white;
        position: absolute;
        bottom: 10.5%;
        cursor: pointer;
        left: 0;
        font-size: 9.5px;
        background-color: transparent;
        text-align: center;
        width: 100%;
        @include xxsm-up {
            font-size: 13px;
            left: 1%;
            bottom: 11%;
        }
    }
}