@import '../../styles/screensizes';

.screen.home {
    .clear-container {
        padding-top: 0;
        .home-content {
            .game-logo {
                margin: -20px 0 -20px;
                width: 140%;
                max-width: 420px;
                height: auto;
                @include xxsm-up {
                    margin: -30px 0 -30px;
                    width: 120%;
                }
                @include xsm-up {
                    margin: -50px 0 -50px;
                }
                @include sm-up {
                    margin: 0 0 -40px;
                }
                @include smd-up {

                }
            }
            .update-message {
                width: 75%;
                box-sizing: border-box;
                background-color: rgba(0,0,0,.4);
                font-weight: normal;
                text-shadow: none;
                padding: 8px 16px;
                border-radius: 7px;
                min-height: 150px;
                a {
                    color: white;
                }
            }
            .studio-logo {
                margin-top: 75px;
                max-width: 140px;
            }
        }
    }
}

.update-message {
    background-color: rgba(0,0,0,.7);
    box-sizing: border-box;
    padding: 10px 20px;
    max-width: 80%;
    border-radius: 6px;
    font-weight: normal;
    margin: 5px 0 15px;
    a{
        color: white;
    }
}