.import-game-content {
    .import-form {
        margin: 10px 0;
    }
    .instructions {
        text-transform: uppercase;
        font-weight: normal;
        margin-bottom: 10px;
        font-size: 1.2em;
    }
    .code-input {
        text-align: center;
        background-color: rgba(0,0,0,.7);
        box-sizing: border-box;
        border-radius: 6px;
        font-family: Adonis;
        text-transform: uppercase;
        width: 300px;
        max-width: 80%;
        padding: 12px 16px;
        outline: none;
        border: none;
        color: white;
        font-size: 1.2em;
    }
    .error {
        color: red;
        text-transform: uppercase;
    }
}