.screen {
    width: 100%;
    min-height: 100%;
    overflow: hidden;

    .backed-container {
        width: 100%;
        min-height: 100%;
        background-image: url('../images/interface/parchment-bg.jpg');
        background-position: center top;
        background-size: cover;
        background-repeat: repeat-y;
        background-attachment: fixed;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        box-sizing: border-box;
        padding-bottom: 30px;
        &.behind {
            z-index: 9;
        }
        &.in-front {
            z-index: 10;
        }
        .content-container {
            margin-top: 46px;
            &.fully {
                margin-top: 71px;
                @include xxsm-up {
                    margin-top: 95px;
                }
                @include xsm-up {
                    margin-top: 111px;
                }
                @include xsm-up {
                    margin-top: 120px;
                }
            }
            &.empty {
                margin-top: 0;
                width: 100%;
            }
        }
        .content {
            padding: 10px;
            box-sizing: border-box;
            color: #403019;
            @include xsm-up {
                padding: 10px 14px;
            }
            @include sm-up {
                padding: 10px 25px;
            }
            h3 {
                font-family: Adonis;
                font-size: 18px;
                text-transform: uppercase;
                font-weight: normal;
            }
        }

        @include sm-up {
            background-size: 465px auto;
        }

        .backed-header {
            background-image: url('../images/interface/stone.jpg');
            background-size: cover;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            color: white;
            position: fixed;
            box-sizing: border-box;
            padding: 0;
            height: 45px;
            z-index: 60;

            @include sm-up {
                width: 465px;
            }

            &.full {
                height: 70px;
                @include xxsm-up {
                    height: 95px;
                }
                @include xsm-up {
                    height: 105px;
                }
                @include sm-up {
                    height: 115px;
                }
            }

            .part-one-container {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                margin: 12px 0 0;
            }


            &-stats {
                font-family: Adonis;
                text-transform: uppercase;
                font-size: 12px;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                margin-left: 6px;
                width: 62px;
                @include xxsm-up {
                    width: 88px;
                }
                @include xsm-up {
                    width: 100px;
                    margin-left: 10px;
                }
                @include sm-up {
                    margin-left: 25px;
                }
                &-p {
                    @include xxsm-up {
                        display: none;
                    }
                }
                &-page {
                    display: none;
                    @include xxsm-up {
                        display: block;
                    }
                }
                &-danger {
                    img {
                        height: 12px;
                        width: auto;
                        @include xxsm-up {
                            height: 15px;
                            position: relative;
                            top: 1px;
                        }
                    }
                    margin-left: 3px;
                    @include xxsm-up {
                        margin-left: 12px;
                    }
                    @include xsm-up {
                        margin-left: 15px;
                    }
                }
            }
            &-turn {
                font-family: Whisky;
                font-size: 12px;
                @include xxsm-up {
                    font-size: 14px;
                }
                @include xsm-up {
                    font-size: 16px;
                }
            }
            &-actions {
                margin-right: 6px;
                width: 52px;
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                @include xxsm-up {
                    width: 88px;
                }
                @include xsm-up {
                    width: 100px;
                    margin-right: 10px;
                }
                @include sm-up {
                    margin-right: 25px;
                }
                &-music {
                    font-size: 12px;
                    margin-left: 10px;
                    opacity: .9;
                    cursor: pointer;
                    position: relative;
                    .muted {
                        color: red;
                    }
                    @include xxsm-up {
                        font-size: 16px;
                    }
                    @include xsm-up {
                        margin-left: 15px;
                    }
                }
                &-back {
                    height: 13px;
                    width: auto;
                    cursor: pointer;
                    @include xxsm-up {
                        height: 18px;
                    }
                }
                &-options {
                    height: 13px;
                    width: auto;
                    cursor: pointer;
                    margin-left: 10px;
                    @include xxsm-up {
                        height: 18px;
                    }
                    @include xsm-up {
                        margin-left: 15px;
                    }
                }
            }

            .part-two-container {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                width: 100%;
                position: relative;
                top: 5px;
                z-index: 12;
                @include xsm-up {
                    top: 10px;
                }
            }

            &-skip {
                cursor: pointer;
                background-image: url('../images/interface/turn-skip.png');
                background-size: contain;
                width: 50%;
                background-repeat: no-repeat;
                color: #91240e;
                background-position: right center;
                text-transform: uppercase;
                text-align: right;
                margin-right: 10px;
                height: 20px;
                font-size: 11px;
                box-sizing: border-box;
                padding: 5px 14px 4px 0;
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                @include xxsm-up {
                    height: 32px;
                    font-size: 16px;
                    padding: 8px 28px 7px 0;
                }
                @include xsm-up {
                    height: 45px;
                    font-size: 19px;
                    padding: 15px 44px 12px 0;
                }
                @include sm-up {
                    height: 47px;
                    font-size: 20px;
                    padding: 18px 50px 13px 0;
                }
            }
            &-end {
                cursor: pointer;
                background-image: url('../images/interface/turn-end.png');
                width: 50%;
                background-size: contain;
                background-repeat: no-repeat;
                color: #336821;
                background-position: left center;
                text-transform: uppercase;
                margin-left: 10px;
                height: 20px;
                font-size: 11px;
                box-sizing: border-box;
                text-align: left;
                padding: 4px 0 4px 14px;
                @include xxsm-up {
                    height: 32px;
                    font-size: 16px;
                    padding: 7px 0 7px 28px;
                }
                @include xsm-up {
                    height: 45px;
                    font-size: 19px;
                    padding: 12px 0 12px 44px;
                }
                @include sm-up {
                    margin-right: -10px;
                    height: 47px;
                    font-size: 20px;
                    padding: 13px 0 13px 50px;
                }
            }

            &-character {
                width: 40px;
                height: auto;
                position: absolute;
                @include xxsm-up {
                    width: 70px;
                }
                @include xsm-up {
                    width: 92px;
                }
            }

            hr {
                background-image: url('../images/interface/divider.png');
                background-repeat: no-repeat;
                background-position: top center;
                height: 5px;
                border: none;
                margin: 0;
                padding: 0;
                position: relative;
                width: 130%;
                @include sm-up {
                    max-width: 465px;
                }
            }
        }



        .left-bar {
            display: none;
            width: 14px;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-image: url('../images/interface/wood-left.png');
            background-size: 100% auto;
            background-repeat: repeat-y;
            z-index: 50;
            @include sm-up {
                display: block;
            }
        }
        .right-bar {
            display: none;
            width: 14px;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            background-image: url('../images/interface/wood-right.png');
            background-size: 100% auto;
            background-repeat: repeat-y;
            z-index: 50;
            @include sm-up {
                display: block;
            }
        }
    }

    .clear-container {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        min-height: 100%;
        box-sizing: border-box;
        padding: 40px 20px;
        color: white;
        font-weight: bold;
        @include smd-up {
            padding: 80px 0px;
        }
        .go-back {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            margin-bottom: 16px;
            &-arrow {
                width: 30px;
                height: auto;
                margin: 0 5px;
            }
            &-words {
                margin: 0 5px;
                font-size: 14px;
                color: white;
                text-transform: uppercase;
            }
        }
        .content {
            h1 {
                color: white;
                font-family: Whisky;
                font-size: 30px;
                font-weight: normal;
                text-align: center;
                margin: 8px 0;
            }
            span {
                text-align: center;
            }
            p {
                color: white;
                text-align: center;
                margin: 8px 0;

                &.error {
                    color: #cc0000;
                    font-weight: bold;
                    background-color: rgba(0, 0, 0, .5);
                    border-radius: 6px;
                    padding: 4px 8px;
                    display: block;
                }
            }
            form {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
            }
            .text-input {
                width: 100%;
                height: 40px;
                font-size: 16px;
                font-family: Adonis;
                text-transform: uppercase;
                border-radius: 6px;
                background-color: rgba(0,0,0,.4);
                text-outline: none;
                border: none;
                color: white;
                text-align: center;
                margin: 8px 0;
            }
        }
    }

    .content {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100%;

        hr {
            background-image: url('../images/interface/divider.png');
            background-repeat: no-repeat;
            background-position: top center;
            height: 5px;
            border: none;
            margin: 8px 0;
            padding: 0;
            position: relative;
            width: 130%;
            @include sm-up {
                    max-width: 460px;
                }
        }

        @include sm-up {
            .quarter {
                max-width: 25%;
            }
            .one-third {
                max-width: 33%;
            }
            .half {
                max-width: 50%;
            }
            .two-thirds {
                max-width: 67%;
            }
            .three-quarters {
                max-width: 75%;
            }
        }


    }
}