@import '../styles/screensizes';

.tutorial-mat {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.3);
    z-index: 9999;
}

.tutorial-container {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    z-index: 10000;
    background-image: url('../images/tutorial/design/box.png');
    background-position: center 0;
    background-repeat: no-repeat;
    padding-top: 42px;
    background-size: 262px auto;
    box-sizing: border-box;
    @include xxsm-up {
        background-size: 346px auto;
        padding-top: 56px;
    }
    @include xsm-up {
        background-size: 462px auto;
        padding-top: 75px;
    }
    @include sm-up {
        background-size: 496px auto;
        padding-top: 80px;
    }
    @include md-up {
        right: 0;
        margin-right: -28px;
        width: 496px;
    }
    @include xlg-up {
        margin-right: 40px;
    }
    @include xxlg-up {
        margin-right: 280px;
    }
    @include xxxlg-up {
        margin-right: 540px;
    }
    &.mini {
        background-image: url('../images/tutorial/design/box-top.png');
        background-size: 226px auto;
        padding-top: 26px;
        @include xxsm-up {
            background-size: 300px auto;
            padding-top: 35px;
        }
        @include xsm-up {
            background-size: 395px auto;
            padding-top: 44px;
        }
        @include sm-up {
            background-size: 426px auto;
        }
    }
    .tutorial {
        user-select: none;
        box-sizing: border-box;
        width: 212px;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        border-bottom: none;
        @include xxsm-up {
            width: 280px;
        }
        @include xsm-up {
            width: 373px;
        }
        @include sm-up {
            width: 401px;
        }
        h3 {
            margin: 0;
        }
        &-back {
            cursor: pointer;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            margin: 10px 0;
            img {
                width: 30px;
                height: auto;
            }
            span {
                text-transform: uppercase;
                font-weight: bold;
                margin-left: 10px;
            }
        }
        .minmax {
            cursor: pointer;
            position: absolute;
            top: -28px;
            left: 8px;
            &.max {
                top: -25px;
                @include xxsm-up {
                    top: -32px;
                }
                @include xsm-up {
                    top: -37px;
                }
            }
            @include xxsm-up {
                top: -36px;
                left: 12px;
            }
            @include xsm-up {
                top: -43px;
                left: 18px;
            }
            @include sm-up {
                top: -45px;
                left: 25px;
            }
            img {
                width: 26px;
                height: auto;
                @include xxsm-up {
                    width: 32px;
                }
                @include xsm-up {
                    width: 36px;
                }
            }
        }
        .title-container {
            position: absolute;
            top: -18px;
            @include xxsm-up {
                top: -23px;
            }
            @include xsm-up {
                top: -30px;
            }
            @include sm-up {
                top: -32px;
            }
            h3 {
                font-size: 14px;
                margin: 0;
                padding: 0;
                font-family: Adonis;
                font-weight: bold;
                @include xxsm-up {
                    font-size: 16px;
                }
                @include xsm-up {
                    font-size: 20px;
                }
            }
        }
        .goback {
            cursor: pointer;
            position: absolute;
            top: 3px;
            left: 8px;
            font-size: 16px;
        }
        .turnoff {
            cursor: pointer;
            position: absolute;
            top: -24px;
            right: 8px;
            @include xxsm-up {
                top: -32px;
                right: 12px;
            }
            @include xsm-up {
                top: -40px;
                right: 18px;
            }
            @include sm-up {
                top: -42px;
                right: 25px;
            }
            img {
                width: 23px;
                height: auto;
                @include xxsm-up {
                    width: 30px;
                }
                @include xsm-up {
                    width: 36px;
                }
            }
        }
        span {
            text-align: center;
        }
        &-screen {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            margin: 0;
            max-height: 200px;
            box-sizing: border-box;
            width: 100%;
            padding-top: 15px;
            @include short-up {
                max-height: 300px;
            }
            @include medheight-up {
                max-height: 500px;
            }
            @include tall-up {
                max-height: 700px;
            }
            overflow-y: scroll;
        }
        &-content {
            padding: 5px 6px;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            @include xxsm-up {
                padding: 5px 3px;
            }
            @include xsm-up {
                padding: 5px 10px;
            }
            img.tutorial-image {
                max-width: 90%;
            }
        }
        &-text {
            text-align: center;
        }
    }
}