.entry-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    .label {
        font-weight: bold;
        font-size: .875em;
    }
    #entry-input {
        border: none;
        height: 40px;
        width: 160px;
        text-align: center;
        border-radius: 6px;
        margin-bottom: 4px;
        font-size: 26px;
    }
    .error {
        margin: 0;
        color: red;
    }
}