.explanation {
    margin-bottom: 20px;
}
.definition {
    width: 100%;
    font-size: 14px;

    text-align: left;
}
.see-also {
    font-weight: bold;
    margin-top: 20px;
    font-size: 14px;
    text-transform: uppercase;
}
    .link {
        cursor: pointer;
        text-decoration: underline;
    }

.glossary-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    .back {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        font-size: 14px;
        text-transform: uppercase;
        cursor: pointer;
        margin: 20px 0 10px;
        img {
            width: 30px;
            height: auto;
            margin: 0 10px 0 0;
        }
    }
    .glossary-search {
        width: 90%;
        height: 44px;
        border-radius: 8px;
        font-size: 30px;
        margin: 2px 0;
        padding: 0 8px;
        box-sizing: border-box;
    }
    .glossary-results {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        width: 90%;
        box-sizing: border-box;
        .result {
            width: 100%;
            height: 40px;
            cursor: pointer;
            margin: 2px 0;
            text-align: center;
            box-sizing: border-box;
            padding: 8px 16px;
            font-size: 20px;
            background-color: rgba(200,200,200,.5);
            border-radius: 8px;
            border: 1px solid rgb(100,100,100);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-transform: capitalize;
            font-weight: bold;
            &:hover {
                background-color: rgba(100,100,100,.5);
            }
        }
    }
}