@import "../styles/screensizes";

.app-button {
    .fw-icon {
        filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(125deg) brightness(108%) contrast(101%);
    }
    &.big-blue {
        background-color: transparent;
        color: white;
        font-family: Whisky;
        border: none;
        background-image: url('../images/interface/button-blue-big.png');
        background-size: 100% 100%;
        box-sizing: border-box;
        text-align: center;
        margin: 8px 0;
        cursor: pointer;
        text-shadow: 2px 1px 2px black;
        text-decoration: none;
        width: 200px;
        height: 40px;
        padding: 10px 26px;
        font-size: 14px;

        @include xxsm-up {
            width: 300px;
            height: 60px;
            padding: 12px 28px;
            font-size: 20px;
        }

        @include xsm-up {
            width: 330px;
            height: 66px;
            font-size: 24px;
            padding: 14px 32px;
        }

    }
    &.icon-button {
        background-color: transparent;
        border: none;
        background-image: url('../images/interface/button-icon.png');
        height: 35px;
        width: 200px;
        background-size: 100% 100%;
        padding-bottom: 8px;
        text-align: center;
        cursor: pointer;
        @include xxsm-up {
            width: 275px;
            height: 48px;
        }
        @include xsm-up {
            width: 350px;
            height: 61px;
            .fw-icon {
                height: 32px;
            }
            padding-bottom: 5px;
        }
    }
    &.green {
        background-color: transparent;
        border: none;
        color: white;
        font-family: Adonis;
        background-image: url('../images/interface/button-green.png');
        &.highlight {
            background-image: url('../images/interface/button-green-highlight.png');
        }
        background-size: 100% 100%;
        box-sizing: border-box;
        text-align: center;
        margin: 8px 0;
        text-transform: uppercase;
        cursor: pointer;
        text-shadow: 2px 1px 2px black;
        text-decoration: none;
        width: 200px;
        height: 40px;
        padding: 10px 24px;
        font-size: 10px;
        line-height: 11px;

        @include xxsm-up {
            width: 272px;
            height: 56px;
            padding: 12px 32px;
            line-height: 15px;
            font-size: 14px;
        }

        &.extra-tall {
            background-image: url('../images/interface/button-green-tall.png');
            height: 58px;
            @include xxsm-up {
                height: 79px;
            }
        }

        &:disabled {
            cursor: default;
            opacity: .6;
        }
    }

    &.small-green {
        background-color: transparent;
        border: none;
        color: white;
        font-family: Adonis;
        background-image: url('../images/interface/button-green.png');
        background-size: 100% 100%;
        box-sizing: border-box;
        text-align: center;
        margin: 4px 0;
        text-transform: uppercase;
        cursor: pointer;
        text-shadow: 2px 1px 2px black;
        text-decoration: none;
        width: 180px;
        height: 36px;
        padding: 9px 22px;
        font-size: 16px;
        line-height: 11px;
    }
}