@import '../../styles/screensizes';

.load-game-content {
    .load-game-container {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
    }
    .load-game-game {
        font-size: 16px;
        color: white;
        text-shadow: 1px 1px 1px black;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        @include xsm-up {
            flex-direction: row;
            justify-content: space-between;
            align-items: flex-end;
            max-width: 400px;
        }
        .game-info {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            &-title {
                text-transform: uppercase;
                margin: 5px 0;
            }
            &-page {
                margin: 5px 0;
            }
            &-code {
                margin: 5px 0;
            }
        }
        .game-actions {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            @include xsm-up {
                width: auto;
            }
        }
    }
}