@import '../../styles/screensizes';

.page {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    @include xsm-up {
        flex-direction: row;
        align-items: flex-start;
        width: 100%;
    }
    &-visual {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
        height: 31px;
        margin-bottom: 8px;
        @include xxsm-up {
            justify-content: flex-start;
            height: auto;
            width: 152px;
            overflow: hidden;
        }
        @include xsm-up {
            margin-bottom: 0;
            flex-shrink: 0;
            margin-right: 20px;
        }
        &-num {
            text-transform: uppercase;
            text-align: center;
            font-size: 14px;
            &.objective {
                text-decoration: underline;
                text-transform: none;
                margin-top: 3px;
                cursor: pointer;
                display: flex;
                flex-direction: row;
                align-items: center;
                justicy-content: center;
                .question-mark {
                    color: #403019;
                    margin-right: 4px;
                }
            }
        }
        &-spread-mini {
            @include xxsm-up {
                display: none;
            }
        }
        &-spread {
            display: none;
            @include xxsm-up {
                display: block;
                height: 78px;
                width: 152px;
                position: relative;
            }
            &-previous {
                font-size: 0;
                background-image: url('../../images/interface/turn-order-arrow-left.png');
                background-size: 100% 100%;
                background-color: transparent;
                border: none;
                width: 27px;
                height: 31px;
                cursor: pointer;
                position: absolute;
                left: 0;
                top: 0;
                z-index: 5;
                @include xxsm-up {
                    left: 22px;
                    top: auto;
                    bottom: 25px;
                }
            }
            &-book {
                width: 152px;
                height: auto;
                position: absolute;
                bottom: 0;
                left: 0;
                z-index: 4;
            }
            &-next {
                font-size: 0;
                background-image: url('../../images/interface/turn-order-arrow-right.png');
                background-size: 100% 100%;
                background-color: transparent;
                border: none;
                width: 27px;
                height: 31px;
                cursor: pointer;
                position: absolute;
                right: 0;
                top: 0;
                z-index: 5;
                @include xxsm-up {
                    right: 22px;
                    top: auto;
                    bottom: 25px;
                }
            }
        }
    }
    &-goal {
        font-size: .875em;
        padding-right: 4px;
        @include xxsm-up {
            min-height: 60px;
        }
        @include xsm-up {
            min-height: 90px;
            margin-top: 22px;
        }

    }
}

.play-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
    flex-wrap: wrap;
    .play-button {
        background-color: transparent;
        border: none;
        font-family: Adonis;
        color: #403019;
        font-size: 16px;
        cursor: pointer;
        margin: 0 5px 15px;
        background-repeat: no-repeat;
        background-position: center top;
        box-sizing: border-box;

        padding-top: 70px;
        width: 120px;
        &.turn-order {
            background-image: url('../../images/interface/button-turn-order.png');
            background-size: 60px auto;
        }
        &.entry-history {
            background-size: 80px auto;
            background-image: url('../../images/interface/button-history.png');
        }
        &.rulebook {
            background-size: 80px auto;
            background-image: url('../../images/interface/button-rules.png');
        }
    }
}