@import '../../styles/screensizes';

.turn-order-content {
    .turn-order-container {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    .turn-order-item {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        position: relative;
        width: 120px;
        margin: 0 8px;
        @include xsm-up {
            margin: 0 26px;
        }
        .turn-order-name {
            text-transform: uppercase;
            color: white;
            text-shadow: 1px 1px 1px black;
            margin-top: 20px;
            text-align: center;
        }
        .turn-order-character {
            height: 115px;
            width: auto;
            margin: 5px 0;
        }
        .turn-order-button-earlier {
            position: absolute;
            left: 0;
            bottom: 47px;
            background-color: transparent;
            border: 0;
            font-size: 0;
            background-image: url('../../images/interface/turn-order-arrow-left.png');
            background-size: 100% 100%;
            background-repeat: no-repeat;
            width: 25px;
            height: 30px;
            cursor: pointer;
        }
        .turn-order-button-later {
            position: absolute;
            right: 0;
            bottom: 47px;
            background-color: transparent;
            border: 0;
            font-size: 0;
            background-image: url('../../images/interface/turn-order-arrow-right.png');
            background-size: 100% 100%;
            background-repeat: no-repeat;
            width: 25px;
            height: 30px;
            cursor: pointer;

        }
    }
}